import { useState } from "react";
import React from "react";
import styled from 'styled-components';
import { Images } from "../../asset/images";
import { Link, useLocation } from 'react-router-dom';

function Nav() {
  const location = useLocation();
  const selectedPage = location.pathname.split('/').pop(); // 현재 URL에서 마지막 경로 부분을 가져옴

  const handleLinkClick = (path, page) => {
    if (selectedPage === page) {
      // 선택된 페이지일 때는 클릭 이벤트를 막음
      return;
    }

    window.location.href = path; // 선택된 페이지가 아니면 새로운 경로로 이동
  };

  return (
    <Container>
      <Title>9 factions</Title>
      <HeaderContainer>
        <div>
          {['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i'].map(page => (
            <Link to={`/harrier/${page}`} key={page} onClick={() => handleLinkClick(`/harrier/${page}`, page)}>
              <Image src={Images[`HARRIER_${page.toUpperCase()}_Nav`]} className={selectedPage === page ? "SelectedPage" : ""} />
            </Link>
          ))}
        </div>
      </HeaderContainer>
    </Container>
  );
}

// 나머지 코드는 동일합니다.

const Container = styled.div`
  padding-top: 24px;
  width: 100%;
  height: 120px;
  background-color: #fff;
  margin-top: -5px;
  z-index: 3;
`

const Image = styled.img`
width: 56px;
height: 56px;

&.SelectedPage {
  border: 2px solid #000;
  border-radius: 6px;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
`

const Title = styled.p`
  padding: 0 20px;
  color: #000;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px; /* 90.909% */
`

const HeaderContainer = styled.div`
  margin-top: 16px;
  padding: 0 20px;
  overflow-x: scroll;
  background-color: #fff;
  height: 80px;



  div {
    display: flex;
    flex-direction: row;
    column-gap: 12px;
    width: max-content;
  }
`

const Item = styled.p`
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 100% */
  color: #000;
  text-decoration-line: ${props => props.selected ? "underline" : "none"};
`


export default Nav