import React from "react";
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import {Images} from "../asset/images";



function FooterBtn({ backChapter, backButtonLabel, nextChapter, nextButtonLabel}) {
  return (
    <Container>
        <Button to= {backChapter}>
            <Image src={Images.ICON_ARROW_LEFT}/>
            <Label>{backButtonLabel}</Label>
        </Button>
        
        <Button to= {nextChapter}>
            <Label>{nextButtonLabel}</Label>
            <Image src={Images.ICON_ARROW_RIGHT}/>
        </Button>
    </Container>
  );
}

const Container = styled.div`
  width: 100%;
  padding: 24px 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  gap: 12px;

`
const Button = styled(Link)`
  cursor: pointer;
  width: 100%;
  border: 1px solid #6A6A6A;
  border-radius: 4px;
  padding: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-family: Charter, Noto Serif KR;
`
const Label = styled.p`
  color: #E1E1E1;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1.12px;`

const Image = styled.img`
width: 16px;
`


export default FooterBtn;